import { Link } from 'react-router-dom';

import Button from './Button';
import './Footer.css';

const Footer = () => {
    return (
        <div className='footer-container'>
            <section className='footer-subscription'>
                <p className='footer-subscription-heading'>
                    Join the adventure newsletter to receive our best vacation deals
                </p>
                <p className='footer-subscription-text'>
                    You can subscribe at any time.
                </p>
                <div className='input-areas'>
                    <form>
                        <input type='email' name='email' placeholder='Your Email' className='footer-input' />
                        <Button buttonStyle='btn--outline'>Subscribe</Button>
                    </form>
                </div>
            </section>
            <div className='footer-links'>
                <div className='footer-link-wrapper'>
                    <div className='footer-link-items'>
                        <h2>About Us</h2>
                        <Link to='/'>How it works</Link>
                        <Link to='/'>Testimonials</Link>
                        <Link to='/'>Careers</Link>
                        <Link to='/'>Investors</Link>
                        <Link to='/'>Terms of Service</Link>
                    </div>
                    <div className='footer-link-items'>
                        <h2>Contact Us</h2>
                        <Link to='/'>Contact</Link>
                        <Link to='/'>Support</Link>
                        <Link to='/'>Destinations</Link>
                        <Link to='/'>Sponsorships</Link>
                    </div>
                </div>
                <div className='footer-link-wrapper'>
                    <div className='footer-link-items'>
                        <h2>Videos</h2>
                        <Link to='/'>Submit Video</Link>
                        <Link to='/'>Ambassadors</Link>
                        <Link to='/'>Agency</Link>
                        <Link to='/'>Influencer</Link>
                    </div>
                    <div className='footer-link-items'>
                        <h2>Social Media</h2>
                        <Link to='/'>Instagram</Link>
                        <Link to='/'>Facebook</Link>
                        <Link to='/'>Youtube</Link>
                        <Link to='/'>Twitter</Link>
                    </div>
                </div>
            </div>
            <section className='social-media'>
                <div className='social-media-wrapper'>
                    <div className='footer-logo'>
                        <Link to='/' className='social-logo'>
                            TRVL <i className='fab fa-typo3'></i>
                        </Link>
                    </div>
                    <small className='website-rights'>TRVL &copy; 2021</small>
                    <div className='social-icons'>
                        <Link to='/' class='social-icon-link facebook' target='_blank' aria-label='Facebook'>
                            <i class='fab fa-facebook-f' />
                        </Link>
                        <Link to='/' class='social-icon-link instagram' target='_blank' aria-label='Instagram'>
                            <i class='fab fa-instagram' />
                        </Link>
                        <Link to='/' class='social-icon-link youtube' target='_blank' aria-label='Youtube'>
                            <i class='fab fa-youtube' />
                        </Link>
                        <Link to='/' class='social-icon-link twitter' target='_blank' aria-label='Twitter'>
                            <i class='fab fa-twitter' />
                        </Link>
                        <Link to='/' class='social-icon-link twitter' target='_blank' aria-label='LinkedIn'>
                            <i class='fab fa-linkedin' />
                        </Link>
                    </div>
                </div>
            </section>
        </div>
    );
};

export default Footer;